// ============= ENHANCED VOICE ASSISTANT COMPONENT =============
const VoiceAssistant = ({ onClose }) => {
  const [assistantState, setAssistantState] = useState('idle');
  const [connectionStatus, setConnectionStatus] = useState('disconnected');
  const [isAutoMode, setIsAutoMode] = useState(false);
  const [interimTranscript, setInterimTranscript] = useState('');
  const [showHelp, setShowHelp] = useState(true);
  const [isVisible, setIsVisible] = useState(true);
  const [isClosing, setIsClosing] = useState(false);
  const [notice, setNotice] = useState(null);
  const [hasPermission, setHasPermission] = useState(false);
  const [retryCount, setRetryCount] = useState(0);
  const [deviceInfo, setDeviceInfo] = useState({});
  const [isKeyboardOpen, setIsKeyboardOpen] = useState(false);
  const [voiceIntensity, setVoiceIntensity] = useState(0);
  const [flatMode, setFlatMode] = useState(false);
  const [motionReduce, setMotionReduce] = useState(false);
  const [isFirstInteraction, setIsFirstInteraction] = useState(true);
  
  const recognitionRef = useRef(null);
  const synthRef = useRef(null);
  const silenceTimeoutRef = useRef(null);
  const isProcessingRef = useRef(false);
  const finalTranscriptRef = useRef('');
  const audioContextRef = useRef(null);
  const autoModeRef = useRef(false);
  const noticeTimeoutRef = useRef(null);
  const wakeLockRef = useRef(null);
  const initialViewportHeight = useRef(window.innerHeight);
  const animationFrameRef = useRef(null);

  // Initialize Enhanced Liquid Glass System
  useEffect(() => {
    // Initialize performance manager
    PerformanceManager.init();
    setFlatMode(document.documentElement.classList.contains('flat-mode'));
    
    // Initialize hero tint sampler
    HeroTintSampler.init('.hero-image', {
      sampleSize: 12,
      saturationBoost: 0.3,
      opacityRange: [0.05, 0.15]
    });
    
    // Check for reduced motion preference
    const mediaQuery = window.matchMedia('(prefers-reduced-motion: reduce)');
    setMotionReduce(mediaQuery.matches);
    
    const handleMotionChange = (e) => setMotionReduce(e.matches);
    mediaQuery.addEventListener('change', handleMotionChange);
    
    return () => {
      mediaQuery.removeEventListener('change', handleMotionChange);
    };
  }, []);

  // Enhanced device detection with mobile optimizations
  useEffect(() => {
    const userAgent = navigator.userAgent.toLowerCase();
    const platform = navigator.platform.toLowerCase();
    
    const deviceInfo = {
      isMobile: /android|webos|iphone|ipad|ipod|blackberry|iemobile|opera mini/i.test(userAgent),
      isIOS: /ipad|iphone|ipod/.test(userAgent) || (platform === 'macintel' && navigator.maxTouchPoints > 1),
      isAndroid: /android/i.test(userAgent),
      isChrome: /chrome/i.test(userAgent) && !/edge/i.test(userAgent),
      isFirefox: /firefox/i.test(userAgent),
      isSafari: /safari/i.test(userAgent) && !/chrome/i.test(userAgent),
      isEdge: /edge/i.test(userAgent),
      hasTouch: 'ontouchstart' in window || navigator.maxTouchPoints > 0,
      isStandalone: window.navigator.standalone === true || window.matchMedia('(display-mode: standalone)').matches,
      supports3D: CSS.supports('transform-style', 'preserve-3d'),
      supportsBackdropFilter: CSS.supports('backdrop-filter', 'blur(10px)'),
      hasHardwareAcceleration: !!window.DeviceOrientationEvent,
      screenSize: {
        width: window.screen.width,
        height: window.screen.height,
        ratio: window.devicePixelRatio || 1
      }
    };
    
    setDeviceInfo(deviceInfo);
    
    if (deviceInfo.isMobile) {
      // Enhanced mobile viewport handling
      if (deviceInfo.isIOS) {
        const viewport = document.querySelector('meta[name="viewport"]');
        if (viewport) {
          viewport.setAttribute('content', 'width=device-width, initial-scale=1, maximum-scale=1, user-scalable=no, viewport-fit=cover');
        }
        
        // iOS specific keyboard detection
        const handleIOSResize = () => {
          const currentHeight = window.visualViewport ? window.visualViewport.height : window.innerHeight;
          const heightDifference = initialViewportHeight.current - currentHeight;
          setIsKeyboardOpen(heightDifference > 150);
        };
        
        if (window.visualViewport) {
          window.visualViewport.addEventListener('resize', handleIOSResize);
          return () => window.visualViewport.removeEventListener('resize', handleIOSResize);
        }
      }
      
      // General mobile keyboard detection
      const handleResize = () => {
        const currentHeight = window.innerHeight;
        const heightDifference = initialViewportHeight.current - currentHeight;
        setIsKeyboardOpen(heightDifference > 150);
      };
      
      const handleOrientationChange = () => {
        setTimeout(() => {
          initialViewportHeight.current = window.innerHeight;
          setIsKeyboardOpen(false);
        }, 500);
      };
      
      window.addEventListener('resize', handleResize);
      window.addEventListener('orientationchange', handleOrientationChange);
      
      return () => {
        window.removeEventListener('resize', handleResize);
        window.removeEventListener('orientationchange', handleOrientationChange);
      };
    }
  }, []);

  // Show notice function with enhanced mobile handling
  const showNotice = useCallback((type, title, content, duration = 3500) => {
    if (noticeTimeoutRef.current) {
      clearTimeout(noticeTimeoutRef.current);
    }
    
    setNotice({ type, title, content, show: true });
    
    // Shorter duration for mobile
    const actualDuration = deviceInfo.isMobile ? Math.min(duration, 3000) : duration;
    
    noticeTimeoutRef.current = setTimeout(() => {
      setNotice(prev => prev ? { ...prev, show: false } : null);
      setTimeout(() => setNotice(null), 300);
    }, actualDuration);
  }, [deviceInfo.isMobile]);

  // Check browser compatibility
  const checkBrowserCompatibility = useCallback(() => {
    if (!window.isSecureContext) {
      showNotice('error', '🔒 HTTPS Required', 'Voice features require a secure connection');
      return false;
    }

    if (!('webkitSpeechRecognition' in window || 'SpeechRecognition' in window)) {
      showNotice('error', '🎙️ Not Supported', 'Speech recognition is not supported');
      return false;
    }

    if (!('speechSynthesis' in window)) {
      showNotice('error', '🔊 Not Supported', 'Speech synthesis is not supported');
      return false;
    }

    showNotice('success', '🌟 Enhanced Glass Ready', 'Compact voice interface with 3D effects!');
    return true;
  }, [showNotice]);

  // Request microphone permission with enhanced error handling
  const requestMicrophonePermission = useCallback(async () => {
    try {
      setConnectionStatus('connecting');
      showNotice('info', '🎙️ Microphone Access', 'Please allow microphone access in the popup to enable voice chat!');
      
      const stream = await navigator.mediaDevices.getUserMedia({
        audio: {
          echoCancellation: true,
          noiseSuppression: true,
          autoGainControl: true,
          // Mobile specific optimizations
          ...(deviceInfo.isMobile && {
            sampleRate: 16000,
            channelCount: 1
          })
        }
      });
      
      const tracks = stream.getTracks();
      tracks.forEach(track => track.stop());
      
      setHasPermission(true);
      setConnectionStatus('connected');
      showNotice('success', '🎤 Ready!', deviceInfo.isMobile ? 'Compact voice ready!' : 'Enhanced  voice interface ready!');
      
      return true;
    } catch (error) {
      console.error('Microphone permission error:', error);
      setHasPermission(false);
      setConnectionStatus('disconnected');
      showNotice('error', '🎤 Permission Denied', 'Please allow microphone access');
      return false;
    }
  }, [showNotice, deviceInfo]);

  // Enhanced audio cues with mobile optimization
  const playAudioCue = useCallback((type) => {
    try {
      const audioContext = audioContextRef.current || new (window.AudioContext || window.webkitAudioContext)();
      if (audioContext.state === 'suspended') {
        audioContext.resume();
      }
      audioContextRef.current = audioContext;
      
      const oscillator = audioContext.createOscillator();
      const gainNode = audioContext.createGain();
      
      oscillator.connect(gainNode);
      gainNode.connect(audioContext.destination);
      
      // Reduced volume for mobile
      const volume = deviceInfo.isMobile ? 0.03 : 0.05;
      const duration = deviceInfo.isMobile ? 0.15 : 0.2;
      
      switch (type) {
        case 'start':
          oscillator.frequency.setValueAtTime(600, audioContext.currentTime);
          oscillator.frequency.exponentialRampToValueAtTime(1200, audioContext.currentTime + 0.1);
          break;
        case 'end':
          oscillator.frequency.setValueAtTime(1200, audioContext.currentTime);
          oscillator.frequency.exponentialRampToValueAtTime(600, audioContext.currentTime + 0.15);
          break;
        case 'error':
          oscillator.frequency.setValueAtTime(300, audioContext.currentTime);
          oscillator.frequency.exponentialRampToValueAtTime(150, audioContext.currentTime + 0.3);
          break;
        default:
          return;
      }
      
      gainNode.gain.setValueAtTime(volume, audioContext.currentTime);
      gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + duration);
      
      oscillator.start(audioContext.currentTime);
      oscillator.stop(audioContext.currentTime + duration);
    } catch (error) {
      console.warn('Audio cue failed:', error);
    }
  }, [deviceInfo]);

  // Content extraction (unchanged)
  const extractContent = useCallback((obj, depth = 0) => {
    if (depth > 5) return null;
    if (typeof obj === 'string' && obj.trim()) {
      return obj.trim();
    }
    if (!obj) return null;
    
    if (obj.result && obj.result.message && obj.result.message.content) {
      return obj.result.message.content.trim();
    }
    
    const patterns = [
      'content',
      'message.content', 
      'result.message.content',
      'choices[0].message.content',
      'text',
      'response',
      'answer',
      'reply'
    ];
    
    for (const pattern of patterns) {
      try {
        const value = pattern.split('.').reduce((acc, key) => {
          if (key.includes('[')) {
            const [arrayKey, index] = key.split('[');
            const idx = parseInt(index.replace(']', ''));
            return acc?.[arrayKey]?.[idx];
          }
          return acc?.[key];
        }, obj);
        
        if (value && typeof value === 'string' && value.trim()) {
          return value.trim();
        }
      } catch (e) {
        // Continue to next pattern
      }
    }
    
    return null;
  }, []);

  // Voice fallback with mobile-optimized responses
  const getVoiceFallback = useCallback((input) => {
    const voiceResponses = {
      greeting: deviceInfo.isMobile ? 
        "Hey! I'm Laxman's AI assistant. What would you like to explore?" :
        "Hey there! I'm Laxman's AI assistant. What would you like to explore?",
      identity: "Laxman is a brilliant software engineer at CAMS in Chennai. He's an IIT Delhi graduate with exceptional AI and full-stack development skills.",
      projects: "He's created amazing AI projects! His flagship CamsLens platform processes over 80,000 documents monthly with incredible 95% accuracy.",
      skills: "He's a master of Java, Python, JavaScript, and cutting-edge AI technologies like Vertex AI,GCP. Truly impressive technical expertise!",
      contact: "You can reach him at laxmankethavath5@gmail.com, or connect on LinkedIn. He's very approachable!",
      location: "He's currently based in Chennai, India, building innovative tech solutions that make a real difference.",
      education: "He completed his dual degree in Computer Science from prestigious IIT Delhi, graduating in 2024. Excellent academic background!",
      experience: "He's been excelling as a software engineer at CAMS since June 2024, previously mentoring over 300 students as a teaching assistant.",
      help: deviceInfo.isMobile ?
        "Ask about Laxman's projects, skills, experience, or contact info. I'm here to help!" :
        "Feel free to ask about Laxman's projects, technical skills, work experience, education, or how to get in touch. I'm here to help!",
      default: "That's interesting! Could you rephrase it? I'm equipped with comprehensive knowledge about Laxman's impressive background!"
    };

    const input_lower = input.toLowerCase();
    
    if (input_lower.includes('hello') || input_lower.includes('hi') || input_lower.includes('hey') || input_lower.includes('greet')) {
      return voiceResponses.greeting;
    }
    if (input_lower.includes('who') || input_lower.includes('about') || input_lower.includes('tell me') || input_lower.includes('describe')) {
      return voiceResponses.identity;
    }
    if (input_lower.includes('project') || input_lower.includes('work') || input_lower.includes('built') || input_lower.includes('develop')) {
      return voiceResponses.projects;
    }
    if (input_lower.includes('skill') || input_lower.includes('technology') || input_lower.includes('programming') || input_lower.includes('technical')) {
      return voiceResponses.skills;
    }
    if (input_lower.includes('contact') || input_lower.includes('reach') || input_lower.includes('email') || input_lower.includes('phone')) {
      return voiceResponses.contact;
    }
    if (input_lower.includes('where') || input_lower.includes('location') || input_lower.includes('live') || input_lower.includes('based')) {
      return voiceResponses.location;
    }
    if (input_lower.includes('education') || input_lower.includes('study') || input_lower.includes('college') || input_lower.includes('university')) {
      return voiceResponses.education;
    }
    if (input_lower.includes('experience') || input_lower.includes('job') || input_lower.includes('career') || input_lower.includes('professional')) {
      return voiceResponses.experience;
    }
    if (input_lower.includes('help') || input_lower.includes('what can') || input_lower.includes('how to') || input_lower.includes('assist')) {
      return voiceResponses.help;
    }
    
    return voiceResponses.default;
  }, [deviceInfo]);

  // Get AI response with mobile optimization
  const getAIResponse = useCallback(async (userInput) => {
    try {
      const maxWords = deviceInfo.isMobile ? 20 : 25;
      const contextPrompt = `You are Laxman's enhanced AI assistant. Respond naturally and engagingly in ${maxWords} words or less:

${LAXMAN_PORTFOLIO_DATA}

Guidelines:
- Keep responses under ${maxWords} words for optimal voice delivery
- Be conversational, enthusiastic, and professional
- Use "he" or "Laxman" when referring to him
- Highlight his achievements and expertise
- Focus on the most relevant and impressive information

User question: "${userInput}"

Provide an engaging, conversational response:`;

      const aiResponse = await window.puter.ai.chat(contextPrompt, {
        model: 'gpt-4o-mini',
        max_tokens: deviceInfo.isMobile ? 80 : 100,
        temperature: 0.8
      });

      const extractedContent = extractContent(aiResponse);
      return extractedContent || getVoiceFallback(userInput);
    } catch (error) {
      console.error('AI API Error:', error);
      return getVoiceFallback(userInput);
    }
  }, [extractContent, getVoiceFallback, deviceInfo]);

  // Stop all functions (unchanged)
  const stopAll = useCallback(() => {
    if (recognitionRef.current) {
      recognitionRef.current.stop();
      recognitionRef.current = null;
    }
    
    window.speechSynthesis.cancel();
    
    if (silenceTimeoutRef.current) {
      clearTimeout(silenceTimeoutRef.current);
      silenceTimeoutRef.current = null;
    }
    
    if (animationFrameRef.current) {
      cancelAnimationFrame(animationFrameRef.current);
      animationFrameRef.current = null;
    }
    
    setAssistantState('idle');
    setInterimTranscript('');
    setVoiceIntensity(0);
    isProcessingRef.current = false;
    finalTranscriptRef.current = '';
  }, []);



  // Start listening with mobile optimizations
  const startListening = useCallback(async () => {
    if (connectionStatus !== 'connected' || assistantState === 'listening' || isProcessingRef.current) {
      return;
    }

    if (!hasPermission) {
      const granted = await requestMicrophonePermission();
      if (!granted) return;
    }

    const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
    
    if (!SpeechRecognition) {
      showNotice('error', '🎙️ Not Supported', 'Speech recognition is not available');
      return;
    }

    const recognition = new SpeechRecognition();
    recognitionRef.current = recognition;

    recognition.continuous = false;
    recognition.interimResults = true;
    recognition.lang = 'en-US';
    recognition.maxAlternatives = 1;

    setAssistantState('listening');
    setInterimTranscript('');
    finalTranscriptRef.current = '';
    setShowHelp(false);
    
    playAudioCue('start');

    recognition.onstart = () => {
      console.log('🎙️ Enhanced speech recognition started');
      if (!deviceInfo.isMobile) {
        recognition.continuous = true;
      }
    };

    recognition.onresult = (event) => {
      let interimText = '';

      for (let i = event.resultIndex; i < event.results.length; i++) {
        const transcript = event.results[i][0].transcript;
        
        if (event.results[i].isFinal) {
          finalTranscriptRef.current += transcript;
        } else {
          interimText += transcript;
        }
      }

      if (interimText) {
        setInterimTranscript(interimText);
      }

      if (silenceTimeoutRef.current) {
        clearTimeout(silenceTimeoutRef.current);
      }

      // Increased timeout for better user experience
      const timeoutDuration = deviceInfo.isMobile ? 5500 : 5500;
      silenceTimeoutRef.current = setTimeout(() => {
        const finalResult = finalTranscriptRef.current.trim();
        if (finalResult && !isProcessingRef.current) {
          if (recognitionRef.current) {
            recognitionRef.current.stop();
          }
          processFinalSpeech(finalResult);
        }
      }, timeoutDuration);
    };

    recognition.onerror = (event) => {
      console.error('Speech recognition error:', event.error);
      
      switch (event.error) {
        case 'no-speech':
          console.log('No speech detected');
          if (autoModeRef.current) {
            setTimeout(() => {
              if (autoModeRef.current && assistantState !== 'speaking') {
                startListening();
              }
            }, 2000);
          } else {
            setAssistantState('idle');
            setInterimTranscript('');
          }
          return;
        
        case 'network':
          showNotice('warning', '🌐 Network Issue', 'Retrying...');
          setTimeout(() => {
            if (assistantState === 'listening') {
              startListening();
            }
          }, 2000);
          return;
        
        default:
          console.error('Unhandled recognition error:', event.error);
          showNotice('warning', '⚠️ Recognition Error', 'Speech recognition encountered an error');
      }
      
      playAudioCue('error');
      setAssistantState('idle');
      setInterimTranscript('');
      isProcessingRef.current = false;
    };

    recognition.onend = () => {
      console.log('🎙️ Speech recognition ended');
      const remainingText = finalTranscriptRef.current.trim();
      if (remainingText && !isProcessingRef.current && assistantState === 'listening') {
        processFinalSpeech(remainingText);
      } else if (!isProcessingRef.current) {
        setAssistantState('idle');
        setInterimTranscript('');
      }
    };

    try {
      recognition.start();
    } catch (error) {
      console.error('Failed to start recognition:', error);
      setAssistantState('idle');
      playAudioCue('error');
      showNotice('error', '🎤 Start Failed', 'Could not start speech recognition');
    }
  }, [connectionStatus, assistantState, hasPermission, requestMicrophonePermission, playAudioCue, showNotice, deviceInfo]);
  // Speak introduction and then start listening
  const speakIntroduction = useCallback(() => {
    const introText = deviceInfo.isMobile ? 
      "Hi! I'm Laxman's AI assistant. How can I help you learn about Laxman?" : 
      "Hey! I'm Laxman's  AI assistant. How can I help you discover Laxman's expertise";
    
    setAssistantState('speaking');
    setIsFirstInteraction(false);
    
    const utterance = new SpeechSynthesisUtterance(introText);
    utterance.rate = deviceInfo.isMobile ? 0.9 : 0.95;
    utterance.pitch = deviceInfo.isMobile ? 1.0 : 1.05;
    utterance.volume = deviceInfo.isMobile ? 0.8 : 0.85;
    utterance.lang = 'en-US';

    utterance.onend = () => {
      console.log('🔊 Introduction complete, starting listening...');
      // Start listening after introduction
      setTimeout(() => {
        startListening();
      }, 500);
    };

    utterance.onerror = (event) => {
      console.error('Introduction speech error:', event.error);
      setAssistantState('idle');
      // Fallback: just start listening
      setTimeout(() => {
        startListening();
      }, 1000);
    };

    window.speechSynthesis.speak(utterance);
  }, [deviceInfo,startListening]);
  // Speak response with mobile optimizations
  const speakResponse = useCallback((text) => {
    if (!text || !text.trim()) {
      setAssistantState('idle');
      return;
    }
    
    if (!('speechSynthesis' in window)) {
      setAssistantState('idle');
      showNotice('error', '🔊 Not Supported', 'Speech synthesis is not available');
      return;
    }

    const cleanText = text.trim();
    window.speechSynthesis.cancel();

    setTimeout(() => {
      const utterance = new SpeechSynthesisUtterance(cleanText);
      synthRef.current = utterance;

      // Mobile optimized speech settings
      utterance.rate = deviceInfo.isMobile ? 0.9 : 0.95;
      utterance.pitch = deviceInfo.isMobile ? 1.0 : 1.05;
      utterance.volume = deviceInfo.isMobile ? 0.8 : 0.85;
      utterance.lang = 'en-US';

      utterance.onstart = () => {
        console.log('🔊 Enhanced speech synthesis started');
      };

      utterance.onend = () => {
        console.log('🔊 Speech synthesis ended');
        setAssistantState('idle');
        
        if (autoModeRef.current) {
          const delay = deviceInfo.isMobile ? 1000 : 1500;
          setTimeout(() => {
            if (autoModeRef.current && assistantState !== 'listening') {
              startListening();
            }
          }, delay);
        }
      };

      utterance.onerror = (event) => {
        console.error('Speech synthesis error:', event.error);
        setAssistantState('idle');
        showNotice('error', '🔊 Speech Error', 'Could not play voice response');
      };

      const setVoice = () => {
        const voices = window.speechSynthesis.getVoices();
        if (voices.length > 0) {
          let preferredVoice = voices.find(voice => 
            voice.lang.includes('en-US') && voice.name.includes('Google')
          ) || voices.find(voice => 
            voice.lang.includes('en-US')
          ) || voices[0];
          
          if (preferredVoice) {
            utterance.voice = preferredVoice;
          }
        }
      };

      if (window.speechSynthesis.getVoices().length > 0) {
        setVoice();
        window.speechSynthesis.speak(utterance);
      } else {
        window.speechSynthesis.onvoiceschanged = () => {
          setVoice();
          window.speechSynthesis.speak(utterance);
        };
        
        setTimeout(() => {
          if (window.speechSynthesis.speaking === false) {
            window.speechSynthesis.speak(utterance);
          }
        }, 300);
      }
    }, 200);
  }, [startListening, assistantState, showNotice, deviceInfo]);

  // Process final speech (unchanged)
  const processFinalSpeech = useCallback(async (finalText) => {
    if (finalText.length < 2 || isProcessingRef.current) return;
    
    isProcessingRef.current = true;
    setInterimTranscript('');
    setShowHelp(false);
    
    playAudioCue('end');
    setAssistantState('thinking');
    
    try {
      console.log('🧠 Processing with enhanced AI:', finalText);
      const aiResponse = await getAIResponse(finalText);
      console.log('🤖 Enhanced response:', aiResponse);
      
      setAssistantState('speaking');
      
      if (aiResponse && aiResponse.trim()) {
        speakResponse(aiResponse);
      } else {
        console.error('No valid response to speak');
        setAssistantState('idle');
      }
    } catch (error) {
      console.error('Error processing speech:', error);
      setAssistantState('idle');
      showNotice('error', '🧠 Processing Error', 'Could not process your request');
    }
    
    isProcessingRef.current = false;
  }, [getAIResponse, playAudioCue, speakResponse, showNotice]);

  // Toggle auto mode with mobile notification
  const toggleAutoMode = useCallback(() => {
    setIsAutoMode(prev => {
      const newState = !prev;
      autoModeRef.current = newState;
      
      if (newState) {
        showNotice('info', '🔄 Auto Mode On', deviceInfo.isMobile ? 'Continuous voice active!' : 'Continuous enhanced voice interaction activated!');
        if (assistantState === 'idle') {
          setTimeout(() => {
            if (isFirstInteraction) {
              speakIntroduction();
            } else {
              startListening();
            }
          }, 1000);
        }
      } else {
        showNotice('info', '⏹️ Auto Mode Off', 'Switched to one-time question mode');
        stopAll();
      }
      return newState;
    });
  }, [assistantState, startListening, stopAll, showNotice, deviceInfo, isFirstInteraction, speakIntroduction]);

  // Test TTS with mobile optimization
  const testTTS = useCallback(() => {
    const testText = deviceInfo.isMobile ? 
      "Hello! Compact voice technology is working perfectly!" : 
      "Hello! voice interface is working excellently with liquid glass design!";
    setAssistantState('speaking');
    speakResponse(testText);
  }, [speakResponse, deviceInfo]);

  // Toggle flat mode
  const toggleFlatMode = useCallback(() => {
    const newFlatMode = PerformanceManager.toggleFlatMode();
    setFlatMode(newFlatMode);
    showNotice('info', newFlatMode ? '📱 Flat Mode On' : '🌟 3D Mode On', 
      newFlatMode ? 'Performance optimized interface' : 'Full enhanced experience');
  }, [showNotice]);

  // Close chat with enhanced animation
  const closeChat = useCallback(() => {
    stopAll();
    setIsClosing(true);
    setTimeout(() => {
      setIsVisible(false);
      if (onClose) {
        onClose();
      }
    }, 400);
  }, [stopAll, onClose]);

  // Get voice status with mobile-optimized text
  const getVoiceStatus = () => {
    switch (assistantState) {
      case 'listening':
        return {
          title: "Listening Active...",
          subtitle: deviceInfo.isMobile ? "Speak clearly!" : "Speak clearly, enhanced analysis active!"
        };
      case 'thinking':
        return {
          title: "AI Processing...",
          subtitle: deviceInfo.isMobile ? "AI analyzing..." : "Enhanced AI finding perfect answer..."
        };
      case 'speaking':
        return {
          title: "Voice Active...",
          subtitle: deviceInfo.isMobile ? "Response playing" : "Delivering enhanced response"
        };
      default:
        return {
          title: deviceInfo.isMobile ? "Laxman's Compact AI" : "Laxman's Enhanced AI",
          subtitle: connectionStatus === 'connected' 
            ? (deviceInfo.isMobile ? "Tap 💬 to start!" : "Click 💬 avatar to start!")
            : connectionStatus === 'connecting'
            ? "Setting up voice features..."
            : (deviceInfo.isMobile ? "Tap 🔒 to enable voice" : "Click 🔒 avatar to enable microphone access to start speaking.")
        };
    }
  };

  // Initialize services (unchanged)
  useEffect(() => {
    const checkServices = async () => {
      if (!checkBrowserCompatibility()) {
        setConnectionStatus('disconnected');
        return;
      }

      const puterReady = window.puter && window.puter.ai;
      
      if (!puterReady) {
        setConnectionStatus('disconnected');
        if (retryCount < 3) {
          setTimeout(() => setRetryCount(prev => prev + 1), 2000);
        } else {
          showNotice('error', '🤖 AI Service', 'Enhanced AI service unavailable. Please refresh the page.');
        }
        return;
      }

      if (hasPermission) {
        setConnectionStatus('connected');
      } else {
        setConnectionStatus('disconnected');
      }
    };
    
    checkServices();
  }, [checkBrowserCompatibility, hasPermission, retryCount, showNotice]);

  // Auto mode effect
  useEffect(() => {
    autoModeRef.current = isAutoMode;
  }, [isAutoMode]);

  // Help timer with mobile adjustment
  useEffect(() => {
    const timer = setTimeout(() => setShowHelp(false), deviceInfo.isMobile ? 6000 : 8000);
    return () => clearTimeout(timer);
  }, [deviceInfo.isMobile]);

  // Cleanup
  useEffect(() => {
    return () => {
      stopAll();
      if (noticeTimeoutRef.current) {
        clearTimeout(noticeTimeoutRef.current);
      }
    };
  }, [stopAll]);

  const statusText = getVoiceStatus();
  const isDisabled = connectionStatus !== 'connected' || !hasPermission;

  if (!isVisible) return null;

  return (
    <>
      <GlobalLiquidGlassStyles />
      
      {/* Enhanced Compact Notice */}
      {notice && (
        <NoticeCard 
          type={notice.type} 
          show={notice.show} 
          isMobile={deviceInfo.isMobile}
          flatMode={flatMode}
        >
          <div className="notice-title">
            {notice.title}
          </div>
          <div className="notice-content">
            {notice.content}
          </div>
        </NoticeCard>
      )}

      {/* Enhanced Compact Voice Assistant Container */}
      <VoiceContainer  
        state={assistantState} 
        isClosing={isClosing} 
        isMobile={deviceInfo.isMobile}
        isKeyboardOpen={isKeyboardOpen}
        flatMode={flatMode}
        motionReduce={motionReduce}
      >
        <ConnectionIndicator 
          status={connectionStatus} 
          isMobile={deviceInfo.isMobile}
          motionReduce={motionReduce}
          flatMode={flatMode}
        />
        
        <CloseButton onClick={closeChat} isMobile={deviceInfo.isMobile} flatMode={flatMode}>×</CloseButton>

        <VoiceAvatar  
          state={assistantState}
          disabled={isDisabled}
          isMobile={deviceInfo.isMobile}
          motionReduce={motionReduce}
          flatMode={flatMode}
          voiceIntensity={voiceIntensity}
          title={isDisabled ? (deviceInfo.isMobile ? "Tap to enable voice chat!" : "Click to enable voice chat!") : ""}
          onClick={async () => {
            if (isDisabled) {
              if (!hasPermission) {
                await requestMicrophonePermission();
              }
              return;
            }

            if (assistantState === 'idle') {
              if (!window.speechSynthesis.speaking) {
                const testUtterance = new SpeechSynthesisUtterance('');
                testUtterance.volume = 0;
                window.speechSynthesis.speak(testUtterance);
              }
              
              // First interaction: speak introduction then listen
              if (isFirstInteraction) {
                speakIntroduction();
              } else {
                // Subsequent interactions: directly start listening
                startListening();
              }
            } else {
              stopAll();
            }
          }}
        >
          {isDisabled ? '🔒' :
           assistantState === 'listening' ? '🎙️' : 
           assistantState === 'thinking' ? '🧠' : 
           assistantState === 'speaking' ? '🗣️' : '💬'}
        </VoiceAvatar >

        <VoiceStatus isMobile={deviceInfo.isMobile} flatMode={flatMode}>
          <div className="status-title">{statusText.title}</div>
          <div className="status-subtitle">{statusText.subtitle}</div>
        </VoiceStatus>

        <VoiceWaves  
          state={assistantState} 
          isMobile={deviceInfo.isMobile}
          motionReduce={motionReduce}
          flatMode={flatMode}
        >
          {[...Array(deviceInfo.isMobile ? 5 : 7)].map((_, i) => (
            <div key={i} className="wave-bar" style={{
              height: `${4 + (voiceIntensity * 24)}px`,
              opacity: assistantState === 'listening' || assistantState === 'speaking' ? 
                0.6 + (voiceIntensity * 0.4) : 0
            }} />
          ))}
        </VoiceWaves >

        <InterimText  
          show={!!interimTranscript} 
          isMobile={deviceInfo.isMobile}
          flatMode={flatMode}
        >
          "{interimTranscript}"
        </InterimText >

        <VoiceControls isMobile={deviceInfo.isMobile} flatMode={flatMode}>
          <ButtonRow isMobile={deviceInfo.isMobile} flatMode={flatMode}>
            <div style={{ textAlign: 'center' }}>
              <VoiceButton 
                onClick={assistantState === 'idle' ? startListening : stopAll}
                disabled={isDisabled}
                isMobile={deviceInfo.isMobile}
                motionReduce={motionReduce}
                flatMode={flatMode}
                title={assistantState === 'idle' ? 'Ask one question' : 'Stop current action'}
              >
                {assistantState === 'idle' ? '🎤' : '⏹️'}
              </VoiceButton >
              <ButtonLabel disabled={isDisabled} isMobile={deviceInfo.isMobile}>
                {deviceInfo.isMobile ? 'One Time' : 'One Time\nQuestion'}
              </ButtonLabel>
            </div>

            <div style={{ textAlign: 'center' }}>
              <VoiceButton 
                active={isAutoMode}
                onClick={toggleAutoMode}
                disabled={isDisabled}
                isMobile={deviceInfo.isMobile}
                motionReduce={motionReduce}
                flatMode={flatMode}
                title="Toggle continuous conversation mode"
              >
                🔄
              </VoiceButton >
              <ButtonLabel disabled={isDisabled} isMobile={deviceInfo.isMobile}>
                {deviceInfo.isMobile ? 'Auto Mode' : 'Auto\nMode'}
              </ButtonLabel>
            </div>

            <div style={{ textAlign: 'center' }}>
              <VoiceButton 
                onClick={testTTS}
                disabled={!('speechSynthesis' in window)}
                isMobile={deviceInfo.isMobile}
                motionReduce={motionReduce}
                flatMode={flatMode}
                title="Test voice output"
              >
                🔊
              </VoiceButton >
              <ButtonLabel disabled={!('speechSynthesis' in window)} isMobile={deviceInfo.isMobile}>
                {deviceInfo.isMobile ? 'Test Voice' : 'Test\nVoice'}
              </ButtonLabel>
            </div>
          </ButtonRow>

          {!deviceInfo.isMobile && (
            <ButtonRow isMobile={deviceInfo.isMobile} flatMode={flatMode}>
              <div style={{ textAlign: 'center' }}>
                <VoiceButton 
                  onClick={toggleFlatMode}
                  isMobile={deviceInfo.isMobile}
                  motionReduce={motionReduce}
                  flatMode={flatMode}
                  title="Toggle 3D/Flat mode"
                  active={!flatMode}
                >
                  {flatMode ? '📱' : '🌟'}
                </VoiceButton >
                <ButtonLabel isMobile={deviceInfo.isMobile}>
                  {flatMode ? 'Flat Mode' : '3D Mode'}
                </ButtonLabel>
              </div>
            </ButtonRow>
          )}
        </VoiceControls>

        <HelpHint 
          show={showHelp && assistantState === 'idle' && connectionStatus === 'connected'} 
          isMobile={deviceInfo.isMobile}
          flatMode={flatMode}
        > 
          🌟 {deviceInfo.isMobile ? 
            'Say "Hello" or ask about Laxman!' : 
            'Experience enhanced voice - ask about Laxman\'s expertise!'
          }
        </HelpHint>
      </VoiceContainer >
    </>
  );
};

// Main Export with Enhanced Liquid Glass
const LiquidGlassSiri = ({ onClose }) => {
  return <VoiceAssistant onClose={onClose} />;
};

export default LiquidGlassSiri;
export { HeroTintSampler, PerformanceManager };